import { Component, OnInit } from "@angular/core";

import { ExpensesService } from "../services/expenses.service";
import { Router, ActivatedRoute } from "@angular/router";
import swal from "sweetalert2";
import { Expenses } from "app/shared/models/expenses.model";

@Component({
  selector: "app-edit-expenses",
  templateUrl: "./edit-expenses.component.html",
  styleUrls: ["./edit-expenses.component.scss"]
})
export class EditExpensesComponent implements OnInit {
  updatedExpenses: Expenses;
  expensesid;
  categoryNameList = [];

  constructor(
    public expensesservice: ExpensesService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedExpenses = new Expenses({});

    this.expensesid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.expensesservice.getExpensesById(this.expensesid).subscribe(
      (data: any) => {
        console.log("expenseslist>>", data);
        this.updatedExpenses = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );

    let i: any;
    this.expensesservice.getCategory().subscribe((data: any) => {
      console.log("categorydata--->", data)
      for (i = 0; i < data.data.length; i++) {
        this.categoryNameList.push(data.data[i].categoryname)
      }
      console.log('NAMESSSS IN LIST ', this.categoryNameList)
    }, (err) => {
      console.log(err)

    })
  }

  update() {
    console.log("Submitted>>>>>>>>>", this.updatedExpenses);

    swal
      .fire({
        title: "Do you want to edit the expenses details ?",
        text: "You won't be able to revert the change",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Confirm"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.expensesservice.editExpenses(this.updatedExpenses).subscribe(
            (data: any) => {
              swal.fire("Edit!", "Expenses has been edited.", "success").then(() => {
                this.router.navigate(['account/manage/expenses']);
              })
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
