import { Component, OnInit } from "@angular/core";
import { ExpensesService } from "../services/expenses.service";
import { Router, ActivatedRoute } from "@angular/router";

import swal from "sweetalert2";
import { Expenses } from "app/shared/models/expenses.model";

@Component({
  selector: "app-add-expenses",
  templateUrl: "./add-expenses.component.html",
  styleUrls: ["./add-expenses.component.scss"]
})
export class AddExpensesComponent implements OnInit {
  newexpenses: Expenses;
  submitting: boolean;
  categoryNameList = [];

  constructor(
    public expensesservice: ExpensesService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.newexpenses = new Expenses({});
  }

  ngOnInit() {
    let i: any;
    this.expensesservice.getCategory().subscribe((data: any) => {
      console.log("categorydata--->", data)
      for (i = 0; i < data.data.length; i++) {
        this.categoryNameList.push(data.data[i].categoryname)
      }
      console.log('NAMESSSS IN LIST ', this.categoryNameList)
    }, (err) => {
      console.log(err)

    })


  }




  submit() {
    console.log("Submitted>>>>>>>>>", this.newexpenses);

    swal
      .fire({
        title: "Do you want to add the expenses ?",
        text: "New expenses will be added in Expenses List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.expensesservice.addExpenses(this.newexpenses).subscribe(
            (data: any) => {
              swal.fire("Add!", "Expenses has been added.", "success").then(() => {
                this.router.navigate(['account/manage/expenses']);
              })
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }





}
