import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { ExpensesService } from "./services/expenses.service";
import swal from "sweetalert2";
import { Expenses } from "../../../shared/models/expenses.model";

@Component({
  selector: "app-expenses",
  templateUrl: "./expenses.component.html",
  styleUrls: ["./expenses.component.scss"]
})
export class ExpensesComponent implements OnInit {
  expensesList: Array<Expenses>;

  constructor(public expensesservice: ExpensesService, private router: Router) {
    this.expensesList = [];
  }

  ngOnInit() {
    this.expensesservice.getExpenses().subscribe(
      (data: any) => {
        console.log("expenseslist>>", data);
        this.expensesList = data.data;
      },
      error => {
        console.log("cant fetch");
      }
    );
  }

  edit(id) {
    console.log("expenses id>>", id);
    this.router.navigate(["account/manage/expenses/edit/" + id]);
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the expenses ?",
        text: "You won't be able to revert the deleted expenses",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.expensesservice.deleteExpenses(id).subscribe(
            (data: any) => {
              swal.fire("Deleted!", "Expenses has been deleted.", "success");
              //dev
              console.log("Dataaa changeddd", data);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Changed!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
